import { useState } from "react";
import Row from "../components/Row";

const App = () => {
  const [rowNum, setRowNum] = useState(8);
  const board = new Array(rowNum);
  const row = new Array(rowNum);
  row.fill("o");
  board.fill(row);

  return (
    <div className="main">
      <input type="number" value={rowNum} />
      {board.map((el, rowNum) => (
        <Row rowData={el} rowNum={rowNum} />
      ))}
    </div>
  );
};

export default App;
